import React from "react";

import Container from "react-bootstrap/Container";
import InputGroup from "react-bootstrap/InputGroup";
import FormControl from "react-bootstrap/FormControl";
import Button from "react-bootstrap/Button";
import Dropdown from "react-bootstrap/Dropdown";
import DropdownButton from "react-bootstrap/DropdownButton";

import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

export default function DropDownInputGroup(props) {
    const [chosenItem, setChosenItem] = React.useState(null);
    const [value, setValue] = React.useState('');

    return <Container className="mb-3">
        <Row>
            <Col>
                <InputGroup>
                    <FormControl
                        placeholder={chosenItem}
                        aria-label="selectedItem"
                        aria-describedby="basic-selectedItem"
                    />
                    <DropdownButton
                        as={InputGroup.Append}
                        variant="outline-secondary"
                        title={props.title}
                        id="input-group-dropdown-2"
                    >
                        {props.items.length > 20 && <FormControl
                            autoFocus
                            className="mx-3 my-2 w-auto"
                            placeholder="Фильтровать по..."
                            onChange={(e) => setValue(e.target.value)}
                            value={value}
                        />}

                        {props.items.length > 20 && value.length > 2 && props.items.filter(item => item.includes(value)).map(item =>
                            <Dropdown.Item
                                key={item}
                                onClick={() => setChosenItem(item)}
                            >{item}</Dropdown.Item>)}


                        {value.length < 3 && props.items.map(item =>
                            <Dropdown.Item
                                key={item}
                                onClick={() => setChosenItem(item)}
                            >{item}</Dropdown.Item>)}

                    </DropdownButton>
                </InputGroup>
            </Col>
            <Col md="auto">
                <Button
                    variant="outline-secondary"
                    onClick={() => props.setSelectedItem(chosenItem)}>
                    {props.buttonTitle}
                </Button>
            </Col>
        </Row>
    </Container>
}